@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
:root{
  font-family: 'Poppins','sans-serif';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  overflow: unset;

  /* Global Variables */
  --PrimaryColor : #6FCF97;
  --Black : #333333;
  --Grey : #BDBDBD;
  --White : #ffffff;

  /* BreakPoints */
  --Phone: 480px;
  --Tablet: 768px;
}

body {
  margin: auto;
  overflow: unset;
  
}

#root{
	height: 100vh;
	width: 100vw;
	overflow: scroll;
	position: relative;
}

h1, h2, h3, h4, h5{
  font-weight: 400;
  margin: 0;
}

h1 {
  font-size: 0.9rem;
  color : #BDBDBD;
  color : var(--Grey);

}

h2 {
  font-size: 1.2rem;
  font-weight: 500;
}

.card{
