@media(max-width: 700px){
  
   ul {
    z-index: 10;
    position: fixed;
    top: 0;
    right: 0;
    padding-right: 0 !important;
    margin: 0;
    height: 100vh;
    width: 100vw;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: -webkit-clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
    transition: clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
    transition: clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s, -webkit-clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
    -webkit-clip-path: circle(0 at 89% 7%);
            clip-path: circle(0 at 89% 7%);
  }
  
  .active{
    -webkit-clip-path: circle(100%);
            clip-path: circle(100%);
  }

  li {
    min-width: 4rem;
    margin-left: 0px !important;
    margin-bottom: 0.8rem;
    font-size: 1.3rem !important;
  }

  nav {
    margin-left: 1rem;
    margin-right: 1rem;
  }

  .rem-form{
	margin: 0;
  	position: absolute;
	top: 0;
	left: 0;
  }
  
  .title{
    display: none;
  }

  .nav-menu-btn {
    z-index: 11;
    right:1rem;
    height: 0.8rem;
    width: 2rem;
    margin-right: 0rem;
    position: relative; }
    .nav-menu-btn > div {
      height: 3px;
      border-width: 1px;
      width: 100%;
      background-color: var(--Black);
      position: absolute;
      transition: all 0.2s cubic-bezier(0.1, 0.82, 0.76, 0.965); }
      
      .nav-menu-btn > div:first-of-type {
      top: 0; }
    
      .nav-menu-btn > div:last-of-type {
      bottom: 0; }
  
      .nav-menu-btn .nav-menu:checked + .first {
        background-color: var(--Black);
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        top: 5px; }
  
      .nav-menu-btn .nav-menu:checked + .first + .second {
        background-color: var(--Black);
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        bottom: 5px; }
  
      .nav-menu-btn .nav-menu {
        width: 100%;
        height: 100%;
        position: relative;
        cursor: pointer;
        z-index: 2;
        -webkit-touch-callout: none;
        -webkit-tap-highlight-color: none;
        -webkit-user-select: none;
            -ms-user-select: none;
                user-select: none;
        position: absolute;
        opacity: 0;
      }
}

@media (min-width: 700px) {
  .nav-menu-btn{
    display: none;
  }
}

